import React, { useState } from "react";
import {
  Button,
  ButtonBase,
  Card,
  CardActionArea,
  CardActions,
  CardContent,
  TextField,
  Typography
 
} from "@material-ui/core";
import {LocationOn} from "@material-ui/icons";
import { useTheme } from "@material-ui/styles";
import {

} from "recharts";

// styles
import useStyles from "./styles";

// components
// import mock from "./mock";
// import Widget from "../../components/Widget";
import PageTitle from "../../components/PageTitle";
import { useHistory } from "react-router-dom/cjs/react-router-dom.min";


export default function Publish(props) {
  var [ forLoc ] = useState(localStorage.getItem("METAP-FOR")|| "");
  var [ toLoc ] = useState(localStorage.getItem("METAP-TO")|| "");

  var history = useHistory();
  var classes = useStyles();

  function onInputClick(e){
    history.push({
      pathname: '/app/searchloc',
      state: {
        mode: e.target.id==="from" ? 'METAP-FOR' : 'METAP-TO'
      }
    });
  }

  return (
    <>
      <PageTitle title="Publish a ride!" />
      <Card sx={{ maxWidth: 345 }}>
      <CardActionArea>
        <CardContent>
          <div className={classes.cardSearch}>
            <LocationOn className={classes.cardSearchIcon}/>
            <TextField
                  id="from"
                  value={forLoc}
                  onClick={onInputClick}
                  margin="normal"
                  placeholder="Leaving from..."
                  fullWidth
            />
          </div>
          <div className={classes.cardSearch}>
            <LocationOn className={classes.cardSearchIcon}/>
            <TextField
              id="to"
              value={toLoc}
              onClick={onInputClick}
              margin="normal"
              placeholder="Going to..."
              fullWidth
            />
          </div>
        </CardContent>
      </CardActionArea>
      <CardActions className={classes.cardSearchBtnbg} >
        <ButtonBase className={classes.cardSearchBtn} >
          Publish ride
        </ButtonBase>
      </CardActions>
    </Card>
    </>
  );
}

