import React, { useState } from "react";

// styles
import useStyles from "./styles";

// components
import PageTitle from "../../components/PageTitle/PageTitle";
import { Avatar, Card, CardActionArea, CardActions, CardContent, CardHeader, CardMedia, Collapse, IconButton, Typography } from "@material-ui/core";
import { ExpandMore, LocationOn } from "@material-ui/icons";

export default function Results() {
  var classes = useStyles();
  // const courseName = "PG-DAC"
  const [list, setList] = useState(new Array('Akshay','Safari','Raj'));
  // const listElements = list.map(element => <div key={'course_item_' + element.replace(' ','_')} className={classes.courseItem} title={element}><span>{element}</span>-<span>{'10 days'}</span></div>);
  
  function renderCard()
  {
    return list.map(element =>
      <Card sx={{ maxWidth: 345 }} className={classes.card}>
        <CardHeader
          avatar={
            <Avatar sx={{ bgcolor: 'red[500]' }} aria-label="recipe">
              {element.charAt(0)}
            </Avatar>
          }
          title={element}
          subheader="September 14, 2016"
        />
        <CardContent>
          <Typography variant="body2" color="text.secondary">
          <div>
                {'10.00 AM'}
                <LocationOn className={classes.cardPlaceIcon}/>
                {'Ahmednagar'}
              </div>
              <span className={classes.cardLine}>|</span>
              <div className={classes.cardPlace}>
                {'10.30 AM'}
                <LocationOn className={classes.cardPlaceIcon}/>
                {'Nanegoan'}
          </div>
          </Typography>
        </CardContent>
        <CardActions disableSpacing>
        </CardActions>
      </Card>
    );
  }
  
  return (
    <div>
      <PageTitle title={"Check the rides"} />
      {renderCard()}
    </div>
  );
}
