import React, { useState } from "react";
import {
  AppBar,
  Toolbar,
  MenuItem,
  InputBase,
  Card,
  CardActionArea,
  CardContent
} from "@material-ui/core";
import {
  ArrowBackIos as ArrowBackIcon,
  Clear as ClearIcon
} from "@material-ui/icons";
import classNames from "classnames";

// styles
import useStyles from "./styles";

export default function Searchtool(props) {
  var classes = useStyles();
  var [searchInput, setSearchInput] = useState('');

  var city = ["Ahmednagar","Akole","Anantapur","Airport road","Ajanery parvat",
  "nagar","nanegoan","nantapuri","nari road","nery parvat"]

  function Matches()  {
    return city.map(element => element.toLowerCase().match(searchInput.toLowerCase()) && 
        <MenuItem 
          key={'menuitem_'+element.replace(' ','_')}
          value={element} 
          onClick={()=>{
            setSearchInput(element);
            localStorage.setItem(props.location?.state?.mode,element);
            // eslint-disable-next-line no-restricted-globals
            history.back();
          }}>{element}
        </MenuItem>
    );
  }

  return (
    <AppBar position="fixed" className={classes.appBar}>
      <Toolbar className={classes.toolbar}>
        <ArrowBackIcon
            classes={{
              root: classNames(
                classes.headerIcon,
                classes.headerIconCollapse,
              ),
            }}
            // eslint-disable-next-line no-restricted-globals
            onClick ={()=>history.back()}
        />
        <div
          className={classNames(classes.search, {
            [classes.searchFocused]: true,
          })}
        >
          <InputBase
            placeholder="Search"
            classes={{
              root: classes.inputRoot,
              input: classes.inputInput,
            }}
            value={searchInput}
            onChange={(e)=>setSearchInput(e.target.value)}
            autoFocus={true}
          />          
        </div>
        {searchInput && <ClearIcon
            classes={{
              root: classNames(
                classes.headerIcon,
                classes.headerIconCollapse,
              ),
            }}
            // eslint-disable-next-line no-restricted-globals
            onClick ={()=>setSearchInput('')}
        />}
      </Toolbar>
      <div id="cities" style={{color :"black"}}>
          <Card sx={{ maxWidth: 345 }}>
            <CardActionArea>
              {
                <CardContent>
                  {searchInput ? Matches() : 
                  (<MenuItem 
                    key="menuitem_current"
                    >Use Current Location
                  </MenuItem>)
                  } 
                </CardContent>
              }
            </CardActionArea>
          </Card>
      </div>
    </AppBar>
  );
}
