import React, { useState } from "react";
import {
  ButtonBase,
  Card,
  CardActionArea,
  CardActions,
  CardContent,
  TextField
 
} from "@material-ui/core";
import {CalendarToday, LocationOn} from "@material-ui/icons";

// styles
import useStyles from "./styles";

// components
import PageTitle from "../../components/PageTitle/PageTitle";
import { useHistory } from "react-router-dom/cjs/react-router-dom.min";
import ReactDatePicker, { CalendarContainer } from "react-datepicker";


export default function Search(props) {
  var [ forLoc ] = useState(localStorage.getItem("META-FOR")|| "");
  var [ toLoc ] = useState(localStorage.getItem("META-TO")|| "");

  var history = useHistory();

  var classes = useStyles();
  // localStorage.removeItem("META-FOR");
  // localStorage.removeItem("META-TO");

  function onInputClick(e){
    history.push({
      pathname: '/app/searchloc',
      state: {
        mode: e.target.id==="from" ? 'META-FOR' : 'META-TO'
      }
    });
  }


  return (
    <>
      <PageTitle title="Find a ride!" />
      <Card sx={{ maxWidth: 345 }}>
      <CardActionArea>
        <CardContent>
          <div className={classes.cardSearch}>
            <LocationOn className={classes.cardSearchIcon}/>
            <TextField
                  id="from"
                  value={forLoc}
                  onClick={onInputClick}
                  margin="normal"
                  placeholder="Leaving from..."
                  fullWidth
            />
          </div>
          <div className={classes.cardSearch}>
            <LocationOn className={classes.cardSearchIcon}/>
            <TextField
              id="to"
              value={toLoc}
              onClick={onInputClick}
              margin="normal"
              placeholder="Going to..."
              fullWidth
            />
          </div>
          <div className={classes.cardSearch}>
              <ReactDatePicker style ={{display: 'flex' , position: 'fixed' }}></ReactDatePicker>

          </div>
        </CardContent>
      </CardActionArea>
      <CardActions className={classes.cardSearchBtnbg} >
        <ButtonBase className={classes.cardSearchBtn}
            onClick={()=> history.push('/app/result')}
        >
          Search
        </ButtonBase>
      </CardActions>
    </Card>
    </>
  );
}

