import React from "react";
import { Grid, IconButton } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import MUIDataTable from "mui-datatables";
import DatePicker from "react-datepicker";
import TimePicker from 'react-time-picker';
import "react-datepicker/dist/react-datepicker.css";

// components
import PageTitle from "../../components/PageTitle";

const datatableData = [
  {
    "id": "632d6a55e36c350dad819120f33",
    "moduleName": "ted",
    "date": '09/23/2022',
    "time": '12:00',
    "link": 'http://localhost:3000/#/app/tables',
    "updateId": "apt_2022-09-23T08:12:05.752496300Z"
},
{
  "id": "632d6a55e3dasda6c350819120f33",
  "moduleName": "tedasdad",
  "date": '09/23/2022',
  "time": '11:00',
  "link": null,
  "updateId": "apt_2022-09-23T08:12:05.752496300Z"
},
{
  "id": "asds",
  "moduleName": "teasddsd",
  "date": '09/23/2022',
  "time": '10:00',
  "link": null,
  "updateId": "apt_2022-09-23T08:12:05.752496300Z"
}];

const useStyles = makeStyles(theme => ({
  tableOverflow: {
    overflow: 'auto'
  }
}))

export default function Tables() {
  const classes = useStyles();
  
  const [value, setValue] = React.useState('10:00');
  const [data, setData] = React.useState([]);
  const [moduleName, setModuleName] = React.useState('');
  const [link, setLink] = React.useState('');
  const [showForm, setShowForm] = React.useState(null); // ADD EDIT null
  const [rowSelected, setRowSelected] = React.useState(null);
  const [startDate, setStartDate] = React.useState(new Date());

  function onRowSelectionChange(e)
  {
    setRowSelected(datatableData[e[0].index]);
  }

function onUpdate(){
  // datatableData.map(ele => {return [ele.moduleName,ele.date,ele.time,ele.link]});
  setRowSelected({
    "id": "632d6a55e36c350819120f33",
    "moduleName": "ted",
    "date": '09/23/2022',
    "time": '10:00',
    "link": null,
    "updateId": "apt_2022-09-23T08:12:05.752496300Z"
})
  if(rowSelected){
    setValue(''); //rowSelected.time
    setStartDate(); // rowSelected.date
    setModuleName(rowSelected.moduleName);
    setLink(rowSelected.link)
  }
  setShowForm('EDIT');
}
  

  return (
    <>
      <PageTitle title="Time Table" />
      <Grid container spacing={4}>
        <Grid item xs={8}>
          <MUIDataTable
            data={datatableData.map(ele => {return [ele.moduleName,ele.date,ele.time,ele.link]})}
            columns={["Module Name", "Date", "Time", "Link"]}
            options={{
              filterType: "checkbox",
              selectableRowsHeader: false,
              selectableRowsHideCheckboxes:false,
              selectableRows:'single',
              pagination: false,
              fixedSelectColumn:true,
              disableToolbarSelect:true,
              onRowSelectionChange:onRowSelectionChange
            }}
          />
          
        </Grid>
        <Grid item md={4}>
          <IconButton onClick={()=>setShowForm('ADD')}>Add</IconButton>
          {rowSelected && <IconButton onClick={()=>onUpdate()}>Update</IconButton>}
          {showForm && (
            
          <form style={{background: 'gainsboro'}}>
          <div className="img-container">
             <label>Module Name : </label>  <input id="date" value={moduleName} type={'type'} />
             
             <label>Date : </label>  <DatePicker selected={startDate} onChange={(e)=>setValue(e.toLocaleDateString())}/>
             
             <label>Time : </label>  <TimePicker value={value} onChange={(e)=>setValue(e)}/>
             
             <label>Link : </label>  <input id="link" value={link} type={'type'} />
            <br/>
            <br/>
            <button className="img-upload-btn">Save</button>
            <br/>
            <button className="img-upload-btn" onClick={()=>setShowForm(null)}>Cancel</button>
          </div>
          </form>)
          }
        </Grid>
      </Grid>
    </>
  );
}
